<template>
  <div class="m-geo">
    <div class="position">
      <i class="el-icon-location" />
      {{$store.state.position.name}}
      <router-link class="changeCity" :to="{name : 'changeCity'}"></router-link>[
      <a href="#" v-for="item in nearCity" :key="item.id">{{item.name }}</a>
      ]
    </div>
    <div class="m-user" v-if="!$store.state.userName">
      <router-link class="login" :to="{name:'login'}"></router-link>
      <router-link class="register" :to="{name: 'register'}"></router-link>
    </div>
    <div class="m-user" v-else>
      {{$store.state.userName}}
      <router-link @click="tuichu" :to="{name:'login'}"></router-link>
    </div>
  </div>
</template>

<script>
import api from "../../api/index.js";
export default {
  data() {
    return {
      nearCity: []
    };
  },

  watch: {
    "$store.state.position": function() {
      this.nearCity = this.$store.state.position.nearCity;
    }
  },
  created() {
    api.getCurPosition().then(res => {
      this.$store.dispatch("setPostion", res.data.data);
      this.nearCity = res.data.data.nearCity;
    });
  },
  methods: {
    tuichu(){
      this.$store.commit('setUserName', '')
    }
  },
};
</script>
