<template>
  <div class="page-register">
    <div class="header">
      <header>
        <a href="#" class="site-logo"></a>
        <div class="login">
          <span>?</span>
          <router-link :to="{name:'login'}"></router-link>
        </div>
      </header>
    </div>
    <div class="content">
      <el-form
        :model="registerFrom"
        status-icon
        :rules="rules"
        ref="registerFrom"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="userName">
          <el-input type="text" v-model="registerFrom.userName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            type="password"
            v-model="registerFrom.password"
            autocomplete="off"
            @input="input"
          ></el-input>
          <div class="pw-strength">
            <div :class="['bar',strengthClass]"></div>
            <div class="letter">
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
        </el-form-item>
        <el-form-item label="" prop="rePassword">
          <el-input type="password" v-model="registerFrom.rePassword" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('registerFrom')"></el-button>
          <p class="trem"> </p>
        </el-form-item>
      </el-form>
    </div>
    <footer>
      <p>meituan.com ICP070791 11010502025545</p>
    </footer>
  </div>
</template>

<script>
import api from "../api/index.js";
export default {
  data() {
    var validateUser = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value.length < 6 || value.length > 16) {
        callback(new Error("4-16!"));
      } else {
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.registerFrom.rePassword !== "") {
          this.$refs.registerFrom.validateField("rePassword");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.registerFrom.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      registerFrom: {
        uesrName: "",
        password: "",
        rePassword: ""
      },
      strengthClass: "",
      rules: {
        uesrName: [{ validator: validateUser, trigger: "blur" }],
        password: [{ validator: validatePass, trigger: "blur" }],
        rePassword: [{ validator: validatePass2, trigger: "blur" }]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          api
            .getRegister({
              params: this.registerFrom
            })
            .then(res => {
              if (res.data.status == "success") {
                this.$router.push({ name: "login" });
              } else {
                alert(res.data.msg);
              }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    input() {
      const regStr = /(\w)+/g;
      const regNum = /(\d)+/g;
      const reg = /_/g;
      let strongth =
        this.registerFrom.password.match(reg) &&
        this.registerFrom.password.match(regStr) &&
        this.registerFrom.password.match(regNum);
      if (
        this.registerFrom.password.length > 15 ||
        (this.registerFrom.password.length >= 6 && strongth)
      ) {
        this.strengthClass = "strong";
      } else if (this.registerFrom.password.length < 6) {
        this.strengthClass = "week";
      } else if (!this.registerFrom.password) {
        this.strengthClass = "";
      } else {
        this.strengthClass = "normal";
      }
    }
  }
};
</script>

<style lang="scss">
@import "../assets/css/register/index.scss";
</style>