<template>
  <div class="catagroy">
    <dl class="m-categroy">
      <dt></dt>
      <dd v-for="(item, index) in list" :key="index">
        <a :href="'#city-'+item.toLowerCase()">{{item}}</a>
      </dd>
    </dl>
    <dl
      class="m-categroy-section"
      v-for="(item, index) in cityGropu"
      :key="index"
      :id="'city-'+index"
    >
      <dt>{{index}}</dt>
      <dd>
        <span @click="changeCity(city)" v-for="(city) in item" :key="city.id">{{city.name}}</span>
      </dd>
    </dl>
  </div>
</template>

<script>
import api from "../../api/index.js";
export default {
  data() {
    return {
      list: "ABCDEFGHIJKLMNOPQRSTUVWXYZ".split(""),
      cityList: [],
      cityGropu: null
    };
  },
  created() {
    api.getCityList().then(res => {
      const obj = {};

      res.data.data.forEach((item, index) => {
        if (!obj[item.firstChar]) {
          obj[item.firstChar] = [];
        }
        obj[item.firstChar].push(item);
      });
      this.cityGropu = obj;
    });
  },
  methods:{
    changeCity(item){
        this.$store.dispatch('setPostion',item)
        this.$router.push({name:'index'})
    }
  },
};
</script>

<style lang="scss">
@import "../../assets/css/changecity/categroy.scss";
</style>