import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    position: {},
    userName: ''
  },
  mutations: {
    setPostion(state, value) {
      state.position = value
    },
    setUserName(state, value) {
      state.userName = value
    }
  },
  actions: {
    setPostion({ state, commit }, value) {
      // 
      commit('setPostion', value)
    }
  },
  modules: {
  }
})
